<template>
  <div class="balance_conatiner">
      <head-top head-title="我的余额" goBack="true"></head-top>
      <section class="blance_bian">
          <section class="blance_bin_lei">
              <section class="blance_colum">
                  <section class="balnce_sp">
                      <header>
                          当前余额
                      </header>
                      <div class="balnce_sp_tx">
                        <img src="../../images/description.png" >
                        <router-link to="/balance/detail" class="balnce_sp_link">
                              余额说明
                        </router-link>
                      </div>
                  </section>
                  <section class="balnce_f">
                      <header>
                          0.00
                      </header>
                      <span>元</span>
                  </section>
                  <section class="balnce_button">
                      提现
                  </section>
              </section>
          </section>
          <section class="blance_mingxi">
              <header>
                  交易明细
              </header>
          </section>
            <div class="blance_login">
                <img src="../../images/no-log.png">
                <p>暂无明细记录</p>
            </div>
      </section>
      <transition name="balance_dong" mode="out-in">
          <router-view></router-view>
      </transition>
  </div>
</template>

<script>
import headTop from '../../components/header/header'
export default {
    components: {
        headTop
    }
}
</script>

<style lang="scss" scopde>
@import '../../style/mixin.scss';
.balance_conatiner{
            padding-top: 1.95rem;
        p, span{
            font-family: Helvetica Neue,Tahoma,Arial;
        }
}
.blance_bian{
    border:0.28rem solid $blue;
    @include wh(100%,7rem);
    .blance_bin_lei{
        background: #fff;
        @include wh(100%,6.5rem);
        padding:0.2rem;
        border:0.025rem solid $blue;
        border-radius: 0.2rem;
        //box-shadow四周的阴影
        //1：水平阴影的位置。允许负值。2：垂直阴影的位置。允许负值。3：模糊距离 4:阴影的尺寸 5:阴影的颜色。请参阅 CSS 颜色值。 6:将外部阴影 (outset) 改为内部阴影  
        box-shadow: 0 0 0 .4em $blue;
        .blance_colum{
            display: flex;
            flex-direction: column;
            padding:0.2rem;
            .balnce_sp{
                display: flex;
                justify-content: space-between;
                align-items: center;
                header{
                    @include sc(0.6rem,#333);
                }
                .balnce_sp_tx{
                    display: flex;
                    align-items: center;
                    img{
                        @include wh(0.6rem,.6rem);
                        padding-right: 0.2rem;
                    }
                    .balnce_sp_link{
                        @include sc(0.5rem,$blue);
                    }
                }
            }
            .balnce_f{
                display: flex;
                margin-top: 0.2rem;
                align-items: center;
                header{
                    @include sc(1.7rem,#333);
                }
                span{
                    @include sc(0.6rem,#333);
                    margin-left: 0.2rem;
                    margin-top: .8rem;
                }
            }
            .balnce_button{
                margin-top: 0.5rem;
                background: #ccc;
                text-align: center;
                padding:0.4rem;
                @include sc(0.7rem,#fff);
                border-radius: 0.2rem;
            }
        }
    }
    .blance_mingxi{
        display: flex;
        header{
            margin-top: 0.7rem;
            @include sc(0.6rem,#ddd);
        }
    }
    .blance_login{
        margin-top: 0.3rem;
        text-align: center;
        p{
            @include sc(0.6rem,#666);
        }
    }
}
.balance_dong-enter-active,.balance_dong-leave-active{
    transition: all .4s;
}
.balance_dong-enter,.balance_dong-leave-active{
    transform: translate3d(2rem,0,0);
    opacity: 0;
}
</style>



